<template>
    <div class="container">
        <!-- <Header :back="true">活动详情</Header> -->

        <div class="activity-page">
            <ul>
                <li v-for="item in promotionList" :key="item.id">
                    <h2>{{item.name}}</h2>
                    <p class="p" v-if="item.startTime && item.endTime">活动时间：{{item.startTime | timer1}} - {{item.endTime | timer1}}</p>
                    <p class="p" v-else>活动时间：永久</p>
                    <div class="pict">
                        <span>{{item.smallImageId}}</span>
                        <image-load :url="item.url"></image-load>
                    </div>
                    <div class="content" v-html="item.content"></div>
                </li>
            </ul>
        </div>
        <div class="fix-box" @click="$router.back()">
            <img src="@/static/img/6daf52_137x135.png" alt="">
        </div>
    </div>
</template>

<script>
    import { mapState } from "vuex";
    import { ACTIVITY_GET_PROMO_TION } from "@/tool/common/constants.js";

    export default {
        data(){
            return {
                user: {
                    uid:'',
                    token:''
                },
                id:''
            }
        },
        created(){
            const { id } = this.$route.query
            this.id = id

            if(this.env.app){
                json = '{"title":"活动详情","rightTitle":{"name":"","url":""},"showNai":"y","back":"h5"}';
                this.callHandle(json);
            }
        },
        computed: {
            ...mapState('Activity', {
                promotionList: function (state) {
                    return state[ACTIVITY_GET_PROMO_TION].promotionList.filter(item => {
                        return item.id == this.id
                    })
                }
            })
        },
        methods: {

        }
    }
</script>

